export default class Board {
    _grid:  Object[][] = [];

    constructor(boardString: string) {
        this._parseGrid(boardString);
    }

    _parseGrid(boardString: string) {
        console.log(boardString.split(/\r?\n/)
            .filter(el => el !== "")
            .map(row => {
                return row.split('')
                    .map(cell => ({ value: cell }))
            })
        );
    }
}
